// TABLES

.table {
  width: 100%;
  text-align: left;
  margin-bottom: 60px;
  background: transparent;
  color: #fff;
  th {
    padding: 10px;
  }
  thead {
    th {
      text-transform: uppercase;
      @include fontSize(12);
      color: $grey-200;
    }
  }
  tr {
    border-bottom: 1px solid #fff;
  }
  .center-align {
    text-align: center;
  }
}