// ROBOT SELECT

.robot-select {
  @include flexbox();
  @include align-items(center);
  padding-bottom: 30px;
  .robot {
    @include flex(0 0 50px);
  }
  .select-field {
    padding-left: 20px;
    @include flex(1 1 auto);
  }
}

.robot {
  text-transform: uppercase;
  font-weight: $bold;
  .sample-color {
    width: 22px;
    height: 22px;
    transform: rotate(45deg);
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px;
  }
  .robot-name {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
  }
}